'use client';

import { useState } from 'react';
import { ChevronLeft, ChevronRight, Star, Quote, Users, TrendingUp, Award, ArrowRight } from 'lucide-react';

export default function TestimonialsSection() {
  const [currentTestimonial, setCurrentTestimonial] = useState(0);

  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const headerHeight = 80;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerHeight;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  const testimonials = [
    {
      name: 'Maria Silva',
      age: 32,
      profession: 'Empresária',
      image: '/api/placeholder/80/80',
      rating: 5,
      text: 'Em 6 meses com o Ricardo Team, perdi 15kg e ganhei muito mais disposição. O acompanhamento personalizado fez toda a diferença no meu resultado.',
      result: 'Perdeu 15kg em 6 meses',
      before: '78kg',
      after: '63kg'
    },
    {
      name: 'João Santos',
      age: 28,
      profession: 'Engenheiro',
      image: '/api/placeholder/80/80',
      rating: 5,
      text: 'Sempre tive dificuldade para ganhar massa muscular. Com o treino personalizado, consegui ganhar 8kg de massa magra em apenas 4 meses.',
      result: 'Ganhou 8kg de massa magra',
      before: '65kg',
      after: '73kg'
    },
    {
      name: 'Ana Costa',
      age: 45,
      profession: 'Médica',
      image: '/api/placeholder/80/80',
      rating: 5,
      text: 'Depois dos 40, achei que seria impossível ter o corpo que sempre sonhei. O Ricardo provou que estava errada. Hoje me sinto mais forte e confiante.',
      result: 'Transformação completa aos 45',
      before: 'Sedentária',
      after: 'Atlética'
    },
    {
      name: 'Carlos Oliveira',
      age: 35,
      profession: 'Advogado',
      image: '/api/placeholder/80/80',
      rating: 5,
      text: 'O treino funcional mudou minha vida. Não só melhorei meu condicionamento físico, como também minha postura e disposição no trabalho.',
      result: 'Melhora na qualidade de vida',
      before: 'Dores nas costas',
      after: 'Sem dores'
    }
  ];

  const stats = [
    {
      icon: Users,
      number: '98%',
      label: 'Taxa de Satisfação',
      description: 'Dos nossos alunos recomendam nossos serviços'
    },
    {
      icon: TrendingUp,
      number: '500+',
      label: 'Clientes Transformados',
      description: 'Pessoas que alcançaram seus objetivos conosco'
    },
    {
      icon: Award,
      number: '4.9',
      label: 'Avaliação Média',
      description: 'Baseada em mais de 200 avaliações'
    }
  ];

  const nextTestimonial = () => {
    setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
  };

  const prevTestimonial = () => {
    setCurrentTestimonial((prev) => (prev - 1 + testimonials.length) % testimonials.length);
  };

  return (
    <section id="depoimentos" className="py-24 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-20">
          <div className="inline-flex items-center space-x-2 bg-primary-red/10 border border-primary-red/20 rounded-full px-4 py-2 mb-6">
            <Star className="w-4 h-4 text-primary-red" />
            <span className="text-sm font-medium text-primary-red">
              Depoimentos
            </span>
          </div>
          
          <h2 className="text-4xl sm:text-5xl font-inter font-bold text-primary-black mb-6">
            Histórias de
            <span className="gradient-text"> Transformação</span>
          </h2>
          
          <p className="text-xl text-secondary-gray-light max-w-3xl mx-auto leading-relaxed">
            Conheça as histórias reais de pessoas que transformaram suas vidas 
            com nosso acompanhamento personalizado.
          </p>
        </div>

        {/* Testimonial Carousel */}
        <div className="relative mb-20">
          <div className="bg-gray-50 rounded-3xl p-12 max-w-5xl mx-auto">
            <div className="flex flex-col lg:flex-row items-center gap-12">
              {/* Quote Icon */}
              <div className="flex-shrink-0">
                <div className="w-20 h-20 bg-gradient-red rounded-2xl flex items-center justify-center iron-glow">
                  <Quote className="w-10 h-10 text-white" />
                </div>
              </div>

              {/* Content */}
              <div className="flex-1 text-center lg:text-left">
                <div className="flex justify-center lg:justify-start mb-4">
                  {[...Array(testimonials[currentTestimonial].rating)].map((_, i) => (
                    <Star key={i} className="w-5 h-5 text-accent-yellow fill-current" />
                  ))}
                </div>

                <blockquote className="text-2xl font-inter text-primary-black mb-8 leading-relaxed">
                  "{testimonials[currentTestimonial].text}"
                </blockquote>

                <div className="flex flex-col sm:flex-row items-center justify-center lg:justify-start gap-8">
                  {/* Profile */}
                  <div className="flex items-center space-x-4">
                    <div className="w-16 h-16 bg-gradient-yellow rounded-full flex items-center justify-center yellow-glow">
                      <span className="text-xl font-bold text-primary-black">
                        {testimonials[currentTestimonial].name.charAt(0)}
                      </span>
                    </div>
                    <div>
                      <h4 className="font-semibold text-primary-black text-lg">
                        {testimonials[currentTestimonial].name}
                      </h4>
                      <p className="text-secondary-gray-light">
                        {testimonials[currentTestimonial].profession}, {testimonials[currentTestimonial].age} anos
                      </p>
                    </div>
                  </div>

                  {/* Results */}
                  <div className="bg-white rounded-xl p-4 border border-gray-200">
                    <p className="text-sm font-medium text-primary-red mb-1">
                      Resultado Alcançado
                    </p>
                    <p className="text-primary-black font-semibold">
                      {testimonials[currentTestimonial].result}
                    </p>
                    <div className="flex items-center space-x-4 mt-2 text-sm text-secondary-gray-light">
                      <span>Antes: {testimonials[currentTestimonial].before}</span>
                      <ArrowRight className="w-4 h-4" />
                      <span>Depois: {testimonials[currentTestimonial].after}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Navigation */}
          <div className="flex items-center justify-center mt-8 space-x-4">
            <button
              onClick={prevTestimonial}
              className="w-12 h-12 bg-white border border-gray-200 rounded-full flex items-center justify-center hover:bg-primary-red hover:text-white hover:border-primary-red transition-all duration-300"
            >
              <ChevronLeft className="w-5 h-5" />
            </button>

            {/* Dots */}
            <div className="flex space-x-2">
              {testimonials.map((_, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentTestimonial(index)}
                  className={`w-3 h-3 rounded-full transition-all duration-300 ${
                    index === currentTestimonial
                      ? 'bg-primary-red w-8'
                      : 'bg-gray-300 hover:bg-gray-400'
                  }`}
                />
              ))}
            </div>

            <button
              onClick={nextTestimonial}
              className="w-12 h-12 bg-white border border-gray-200 rounded-full flex items-center justify-center hover:bg-primary-red hover:text-white hover:border-primary-red transition-all duration-300"
            >
              <ChevronRight className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Statistics */}
        <div className="grid md:grid-cols-3 gap-8 mb-20">
          {stats.map((stat, index) => (
            <div key={index} className="text-center">
              <div className={`w-20 h-20 mx-auto mb-6 rounded-2xl flex items-center justify-center ${
                index === 1 ? 'bg-gradient-yellow yellow-glow' : 'bg-gradient-red iron-glow'
              }`}>
                <stat.icon className={`w-10 h-10 ${
                  index === 1 ? 'text-primary-black' : 'text-white'
                }`} />
              </div>
              
              <div className="text-4xl font-inter font-bold text-primary-black mb-2">
                {stat.number}
              </div>
              
              <h3 className="text-xl font-semibold text-primary-black mb-2">
                {stat.label}
              </h3>
              
              <p className="text-secondary-gray-light">
                {stat.description}
              </p>
            </div>
          ))}
        </div>

        {/* CTA Section */}
        <div className="bg-gradient-dark rounded-3xl p-12 text-center text-white">
          <div className="max-w-4xl mx-auto">
            <h3 className="text-3xl sm:text-4xl font-inter font-bold mb-6">
              Pronto para ser o próximo caso de sucesso?
            </h3>
            
            <p className="text-xl text-secondary-gray-light mb-8 leading-relaxed">
              Junte-se a centenas de pessoas que já transformaram suas vidas. 
              Comece sua jornada de transformação hoje mesmo.
            </p>

            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <button
                onClick={() => scrollToSection('planos')}
                className="group bg-gradient-yellow text-primary-black px-8 py-4 rounded-xl font-semibold text-lg hover:scale-105 transition-all duration-300 gym-shadow-yellow flex items-center justify-center space-x-2"
              >
                <span>Começar Agora</span>
                <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform duration-200" />
              </button>
              
              <button
                onClick={() => scrollToSection('contato')}
                className="bg-transparent border-2 border-primary-red text-white px-8 py-4 rounded-xl font-semibold text-lg hover:bg-primary-red hover:scale-105 transition-all duration-300"
              >
                Falar com Especialista
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}