'use client';

import { ArrowRight, Play, Star, Users, Trophy, Target } from 'lucide-react';

export default function HeroSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const headerHeight = 80;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerHeight;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  const openWhatsApp = () => {
    const whatsappUrl = `https://wa.me/5511999999999?text=${encodeURIComponent('Olá! Gostaria de saber mais sobre os treinos do Ricardo Team.')}`;
    window.open(whatsappUrl, '_blank');
  };

  return (
    <section id="inicio" className="relative min-h-screen flex items-center hero-bg overflow-hidden">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-5">
        <div className="steel-texture w-full h-full"></div>
      </div>

      <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Content */}
          <div className="space-y-8">
            {/* Badge */}
            <div className="inline-flex items-center space-x-2 bg-primary-red/10 border border-primary-red/20 rounded-full px-4 py-2">
              <Star className="w-4 h-4 text-accent-yellow" />
              <span className="text-sm font-medium text-white">
                #1 Personal Trainers em São Paulo
              </span>
            </div>

            {/* Main Heading */}
            <div className="space-y-4">
              <h1 className="text-4xl sm:text-5xl lg:text-6xl font-inter font-bold leading-tight">
                <span className="text-white">Transforme Seu</span>
                <br />
                <span className="gradient-text">Corpo e Mente</span>
                <br />
                <span className="text-white">Com o</span>
                <span className="gradient-text-yellow"> Ricardo Team</span>
              </h1>
              
              <p className="text-lg sm:text-xl text-secondary-gray-light leading-relaxed max-w-2xl">
                Treinos personalizados, acompanhamento profissional e resultados comprovados. 
                Mais de <span className="text-accent-yellow font-semibold">500 vidas transformadas</span> através 
                do fitness e bem-estar.
              </p>
            </div>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row gap-4">
              <button
                onClick={openWhatsApp}
                className="group btn-primary btn-lg btn-glow flex items-center justify-center space-x-2"
              >
                <span>Começar Transformação</span>
                <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform duration-200" />
              </button>
              
              <button
                onClick={() => scrollToSection('sobre')}
                className="group btn-outline-red btn-lg flex items-center justify-center space-x-2"
              >
                <Play className="w-5 h-5" />
                <span>Ver Como Funciona</span>
              </button>
            </div>

            {/* Social Proof */}
            <div className="flex items-center space-x-8 pt-8">
              <div className="flex items-center space-x-2">
                <div className="flex -space-x-2">
                  {[1, 2, 3, 4].map((i) => (
                    <div
                      key={i}
                      className="w-10 h-10 rounded-full bg-gradient-red border-2 border-white flex items-center justify-center"
                    >
                      <span className="text-white text-sm font-semibold">{i}</span>
                    </div>
                  ))}
                </div>
                <div className="ml-3">
                  <p className="text-white font-semibold">500+ Clientes</p>
                  <p className="text-secondary-gray-light text-sm">Transformados</p>
                </div>
              </div>

              <div className="flex items-center space-x-1">
                {[1, 2, 3, 4, 5].map((i) => (
                  <Star key={i} className="w-5 h-5 text-accent-yellow fill-current" />
                ))}
                <span className="ml-2 text-white font-semibold">4.9/5</span>
                <span className="text-secondary-gray-light text-sm ml-1">(200+ avaliações)</span>
              </div>
            </div>
          </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-2 gap-6">
            <div className="bg-secondary-gray-metal/30 backdrop-blur-sm border border-secondary-gray-metal rounded-2xl p-6 card-hover">
              <div className="w-12 h-12 bg-gradient-red rounded-xl flex items-center justify-center iron-glow mb-4">
                <Users className="w-6 h-6 text-white" />
              </div>
              <h3 className="text-2xl font-bold text-white mb-2">500+</h3>
              <p className="text-secondary-gray-light">Clientes Ativos</p>
            </div>

            <div className="bg-secondary-gray-metal/30 backdrop-blur-sm border border-secondary-gray-metal rounded-2xl p-6 card-hover">
              <div className="w-12 h-12 bg-gradient-yellow rounded-xl flex items-center justify-center yellow-glow mb-4">
                <Trophy className="w-6 h-6 text-primary-black" />
              </div>
              <h3 className="text-2xl font-bold text-white mb-2">98%</h3>
              <p className="text-secondary-gray-light">Taxa de Sucesso</p>
            </div>

            <div className="bg-secondary-gray-metal/30 backdrop-blur-sm border border-secondary-gray-metal rounded-2xl p-6 card-hover">
              <div className="w-12 h-12 bg-gradient-red rounded-xl flex items-center justify-center iron-glow mb-4">
                <Target className="w-6 h-6 text-white" />
              </div>
              <h3 className="text-2xl font-bold text-white mb-2">5+</h3>
              <p className="text-secondary-gray-light">Anos de Experiência</p>
            </div>

            <div className="bg-secondary-gray-metal/30 backdrop-blur-sm border border-secondary-gray-metal rounded-2xl p-6 card-hover">
              <div className="w-12 h-12 bg-gradient-yellow rounded-xl flex items-center justify-center yellow-glow mb-4">
                <Star className="w-6 h-6 text-primary-black" />
              </div>
              <h3 className="text-2xl font-bold text-white mb-2">4.9</h3>
              <p className="text-secondary-gray-light">Avaliação Média</p>
            </div>
          </div>
        </div>
      </div>

      {/* Scroll Indicator */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-white/30 rounded-full flex justify-center">
          <div className="w-1 h-3 bg-white rounded-full mt-2 animate-pulse"></div>
        </div>
      </div>
    </section>
  );
}