'use client';

import { Award, Heart, Target, Clock, CheckCircle, ArrowRight } from 'lucide-react';

export default function AboutSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const headerHeight = 80;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerHeight;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  const features = [
    {
      icon: Award,
      title: 'Certificação Profissional',
      description: 'Personal trainers certificados com anos de experiência e formação continuada.',
      color: 'red'
    },
    {
      icon: Heart,
      title: 'Acompanhamento Personalizado',
      description: 'Monitoramento constante do seu progresso com ajustes personalizados.',
      color: 'yellow'
    },
    {
      icon: Target,
      title: 'Resultados Comprovados',
      description: 'Metodologia testada com mais de 500 transformações bem-sucedidas.',
      color: 'red'
    },
    {
      icon: Clock,
      title: 'Horários Flexíveis',
      description: 'Treinos adaptados à sua rotina, com horários que funcionam para você.',
      color: 'yellow'
    }
  ];

  const benefits = [
    'Avaliação física completa',
    'Plano de treino personalizado',
    'Acompanhamento nutricional',
    'Suporte via WhatsApp',
    'Flexibilidade de horários',
    'Resultados em 30 dias'
  ];

  const stats = [
    { number: '500+', label: 'Clientes Transformados' },
    { number: '98%', label: 'Taxa de Satisfação' },
    { number: '5+', label: 'Anos de Experiência' },
    { number: '24/7', label: 'Suporte Disponível' }
  ];

  return (
    <section id="sobre" className="py-24 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-20">
          <div className="inline-flex items-center space-x-2 bg-primary-red/10 border border-primary-red/20 rounded-full px-4 py-2 mb-6">
            <Award className="w-4 h-4 text-primary-red" />
            <span className="text-sm font-medium text-primary-red">
              Sobre o Ricardo Team
            </span>
          </div>
          
          <h2 className="text-4xl sm:text-5xl font-inter font-bold text-primary-black mb-6">
            Transformando Vidas Através do
            <span className="gradient-text"> Fitness</span>
          </h2>
          
          <p className="text-xl text-secondary-gray-light max-w-3xl mx-auto leading-relaxed">
            Somos especialistas em transformação corporal e mental, oferecendo treinos personalizados 
            que se adaptam ao seu estilo de vida e objetivos únicos.
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8 mb-20">
          {features.map((feature, index) => (
            <div
              key={index}
              className="group bg-white border border-gray-100 rounded-2xl p-8 card-hover text-center"
            >
              <div className={`w-16 h-16 mx-auto mb-6 rounded-2xl flex items-center justify-center ${
                feature.color === 'red' 
                  ? 'bg-gradient-red iron-glow' 
                  : 'bg-gradient-yellow yellow-glow'
              }`}>
                <feature.icon className={`w-8 h-8 ${
                  feature.color === 'red' ? 'text-white' : 'text-primary-black'
                }`} />
              </div>
              
              <h3 className="text-xl font-inter font-semibold text-primary-black mb-4">
                {feature.title}
              </h3>
              
              <p className="text-secondary-gray-light leading-relaxed">
                {feature.description}
              </p>
            </div>
          ))}
        </div>

        {/* Main Content */}
        <div className="grid lg:grid-cols-2 gap-16 items-center mb-20">
          {/* Left Content */}
          <div className="space-y-8">
            <div>
              <h3 className="text-3xl font-inter font-bold text-primary-black mb-6">
                Por que escolher o Ricardo Team?
              </h3>
              
              <p className="text-lg text-secondary-gray-light leading-relaxed mb-8">
                Nossa abordagem vai além do treino tradicional. Combinamos ciência do exercício, 
                nutrição e acompanhamento psicológico para garantir resultados duradouros.
              </p>
            </div>

            {/* Benefits List */}
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              {benefits.map((benefit, index) => (
                <div key={index} className="flex items-center space-x-3">
                  <CheckCircle className="w-5 h-5 text-primary-red flex-shrink-0" />
                  <span className="text-secondary-gray-light">{benefit}</span>
                </div>
              ))}
            </div>

            {/* CTA */}
            <button
              onClick={() => scrollToSection('planos')}
              className="group bg-gradient-yellow text-primary-black px-8 py-4 rounded-xl font-semibold text-lg hover:scale-105 transition-all duration-300 gym-shadow-yellow flex items-center space-x-2"
            >
              <span>Ver Nossos Planos</span>
              <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform duration-200" />
            </button>
          </div>

          {/* Right Content - Stats */}
          <div className="grid grid-cols-2 gap-6">
            {stats.map((stat, index) => (
              <div
                key={index}
                className="bg-gradient-dark text-white rounded-2xl p-8 text-center card-hover"
              >
                <div className="text-4xl font-bold gradient-text-yellow mb-2">
                  {stat.number}
                </div>
                <div className="text-secondary-gray-light text-sm">
                  {stat.label}
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Bottom CTA Section */}
        <div className="bg-gradient-dark rounded-3xl p-12 text-center text-white">
          <h3 className="text-3xl font-inter font-bold mb-4">
            Pronto para começar sua
            <span className="gradient-text-yellow"> transformação</span>?
          </h3>
          
          <p className="text-xl text-secondary-gray-light mb-8 max-w-2xl mx-auto">
            Agende uma avaliação gratuita e descubra como podemos ajudar você a alcançar seus objetivos.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <button
              onClick={() => scrollToSection('planos')}
              className="btn-primary btn-lg btn-glow"
            >
              Avaliação Gratuita
            </button>
            
            <button
              onClick={() => scrollToSection('depoimentos')}
              className="btn-outline-red btn-lg"
            >
              Ver Depoimentos
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}