'use client';

import { Dumbbell, Heart, Zap, Target, Check, ArrowRight, Clock } from 'lucide-react';

export default function TrainingTypesSection() {
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const headerHeight = 80;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerHeight;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  const trainingTypes = [
    {
      icon: Dumbbell,
      title: 'Musculação',
      description: 'Treinos focados em hipertrofia e ganho de força muscular.',
      features: [
        'Treino personalizado',
        'Progressão controlada',
        'Técnica aperfeiçoada',
        'Resultados em 8 semanas'
      ],
      color: 'red',
      duration: '45-60 min',
      level: 'Todos os níveis'
    },
    {
      icon: Heart,
      title: 'Emagrecimento',
      description: 'Combinação perfeita de cardio e resistência para queima de gordura.',
      features: [
        'Queima acelerada de gordura',
        'Melhora cardiovascular',
        'Aumento do metabolismo',
        'Definição muscular'
      ],
      color: 'yellow',
      duration: '40-50 min',
      level: 'Iniciante a avançado'
    },
    {
      icon: Zap,
      title: 'Funcional',
      description: 'Movimentos naturais que melhoram sua performance no dia a dia.',
      features: [
        'Melhora da coordenação',
        'Fortalecimento do core',
        'Flexibilidade aumentada',
        'Prevenção de lesões'
      ],
      color: 'red',
      duration: '35-45 min',
      level: 'Todos os níveis'
    },
    {
      icon: Target,
      title: 'Performance',
      description: 'Treinos de alta intensidade para atletas e esportistas.',
      features: [
        'Explosão e potência',
        'Resistência específica',
        'Agilidade aprimorada',
        'Recuperação otimizada'
      ],
      color: 'yellow',
      duration: '50-70 min',
      level: 'Intermediário a avançado'
    }
  ];

  return (
    <section id="treinos" className="py-24 bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-20">
          <div className="inline-flex items-center space-x-2 bg-primary-red/10 border border-primary-red/20 rounded-full px-4 py-2 mb-6">
            <Dumbbell className="w-4 h-4 text-primary-red" />
            <span className="text-sm font-medium text-primary-red">
              Tipos de Treino
            </span>
          </div>
          
          <h2 className="text-4xl sm:text-5xl font-inter font-bold text-primary-black mb-6">
            Encontre o Treino
            <span className="gradient-text"> Perfeito</span> para Você
          </h2>
          
          <p className="text-xl text-secondary-gray-light max-w-3xl mx-auto leading-relaxed">
            Oferecemos diferentes modalidades de treino, todas personalizadas para seus objetivos 
            e nível de condicionamento físico.
          </p>
        </div>

        {/* Training Types Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8 mb-20">
          {trainingTypes.map((training, index) => (
            <div
              key={index}
              className="group bg-white rounded-3xl p-8 card-hover border border-gray-100"
            >
              {/* Icon */}
              <div className={`w-16 h-16 mb-6 rounded-2xl flex items-center justify-center ${
                training.color === 'red' 
                  ? 'bg-gradient-red iron-glow' 
                  : 'bg-gradient-yellow yellow-glow'
              }`}>
                <training.icon className={`w-8 h-8 ${
                  training.color === 'red' ? 'text-white' : 'text-primary-black'
                }`} />
              </div>

              {/* Content */}
              <div className="space-y-4">
                <h3 className="text-2xl font-inter font-bold text-primary-black">
                  {training.title}
                </h3>
                
                <p className="text-secondary-gray-light leading-relaxed">
                  {training.description}
                </p>

                {/* Training Info */}
                <div className="space-y-2 pt-2">
                  <div className="flex items-center space-x-2 text-sm text-secondary-gray-light">
                    <Clock className="w-4 h-4" />
                    <span>{training.duration}</span>
                  </div>
                  <div className="flex items-center space-x-2 text-sm text-secondary-gray-light">
                    <Target className="w-4 h-4" />
                    <span>{training.level}</span>
                  </div>
                </div>

                {/* Features */}
                <div className="space-y-3 pt-4">
                  {training.features.map((feature, featureIndex) => (
                    <div key={featureIndex} className="flex items-center space-x-3">
                      <Check className="w-4 h-4 text-primary-red flex-shrink-0" />
                      <span className="text-sm text-secondary-gray-light">{feature}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* CTA Section */}
        <div className="bg-gradient-dark rounded-3xl p-12 text-center text-white">
          <div className="max-w-4xl mx-auto">
            <h3 className="text-3xl sm:text-4xl font-inter font-bold mb-6">
              Não sabe qual treino é ideal para você?
            </h3>
            
            <p className="text-xl text-secondary-gray-light mb-8 leading-relaxed">
              Nossa equipe fará uma avaliação completa e criará um plano personalizado 
              baseado nos seus objetivos, limitações e preferências.
            </p>

            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <button
                onClick={() => scrollToSection('planos')}
                className="group btn-primary btn-lg btn-glow flex items-center justify-center space-x-2"
              >
                <span>Ver Planos e Preços</span>
                <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform duration-200" />
              </button>
              
              <button
                onClick={() => scrollToSection('depoimentos')}
                className="btn-outline-red btn-lg"
              >
                Ver Depoimentos
              </button>
            </div>
          </div>
        </div>

        {/* Additional Info */}
        <div className="grid md:grid-cols-3 gap-8 mt-20">
          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-red rounded-2xl flex items-center justify-center iron-glow mx-auto mb-4">
              <Target className="w-8 h-8 text-white" />
            </div>
            <h4 className="text-xl font-inter font-semibold text-primary-black mb-2">
              Avaliação Gratuita
            </h4>
            <p className="text-secondary-gray-light">
              Análise completa do seu condicionamento físico e definição de objetivos.
            </p>
          </div>

          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-yellow rounded-2xl flex items-center justify-center yellow-glow mx-auto mb-4">
              <Dumbbell className="w-8 h-8 text-primary-black" />
            </div>
            <h4 className="text-xl font-inter font-semibold text-primary-black mb-2">
              Treino Personalizado
            </h4>
            <p className="text-secondary-gray-light">
              Plano de exercícios criado especificamente para você e seus objetivos.
            </p>
          </div>

          <div className="text-center">
            <div className="w-16 h-16 bg-gradient-red rounded-2xl flex items-center justify-center iron-glow mx-auto mb-4">
              <Heart className="w-8 h-8 text-white" />
            </div>
            <h4 className="text-xl font-inter font-semibold text-primary-black mb-2">
              Acompanhamento Contínuo
            </h4>
            <p className="text-secondary-gray-light">
              Monitoramento constante do progresso com ajustes quando necessário.
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}